import { getTopMv } from '@/request/api/video.js';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      // 可查地区
      areas: ['内地', '港台', '欧美', '日本', '韩国'],
      // 查找地区
      area: '无',
      // mv 排行
      topMv: []
    };
  },

  mounted() {
    // 获取 mv 排行
    this.getTopMv(50, '');
  },

  methods: {
    // 获取 mv 排行
    async getTopMv(limit, area) {
      if (this.area !== area) {
        // 显示加载
        this.isLoading = 1;
        this.area = area; // 第二个参数不填则为全部

        const res = await getTopMv(limit, this.area); // console.log('获取mv 排行', res);

        this.topMv = res.data.data; // console.log('mv 排行', this.topMv);
        // 加载完成

        this.isLoading = 0;
      }
    },

    // 鼠标放上 area 文字变黑
    areaMouseover(index) {
      if (this.$refs.area[index].style.color != 'rgb(231, 25, 25)') this.$refs.area[index].style.color = '#000';
    },

    // 鼠标移开 area 字色恢复
    areaMouseleave(index) {
      if (this.$refs.area[index].style.color != 'rgb(231, 25, 25)') this.$refs.area[index].style.color = '#929292';
    },

    // 对排行名次的处理
    changeNumTop(num) {
      return this.$formatIndex(num);
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};